import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Title }     from '@angular/platform-browser';

import { GetEventsService } from './../get-events.service';
import { CulturalEvent } from './../event-type';

@Component({
    selector: 'app-events-list',
    templateUrl: './events-list.component.html',
    styleUrls: ['./events-list.component.scss']
})
export class EventsListComponent implements OnInit {

    constructor(
        private getEventsService: GetEventsService,
        private titleService: Title
        ) { }

    events: CulturalEvent[];
    getEvents() {
        this.getEventsService.getEvents().subscribe(events => this.events = events);
    }

    @ViewChild('mycanvas') canvasRef: ElementRef;
    @ViewChild('secondcanvas') SecondCanvasRef: ElementRef;

    ngOnInit() {
        this.titleService.setTitle( "Cultural Events" )
        this.getEvents();

        let w: number = window.innerWidth;
        let h: number = window.innerHeight;

        let canvas = this.canvasRef.nativeElement;
        let ctx: CanvasRenderingContext2D = canvas.getContext('2d');

        let gradientColors = [
            ['#391552', '#914C8A'],
            ['#151E64', '#125074'],
            ['#66244d', '#246655'],
            ['#00B294', '#6CFFE6'],
            ['#5f2C82', '#49A09D'],
            ['#DA22FF', '#9733EE'],
            ['#2B5876', '#4E4376'],
            ['#C33764', '#1D2671']
        ]

        let gc = gradientColors[Math.floor(Math.random() * 8)];
        //let gc = gradientColors[4];

        let gradient = ctx.createLinearGradient(0, 0, canvas.width, canvas.height);
        gradient.addColorStop(0, gc[0]);
        gradient.addColorStop(1, gc[1]);
        ctx.fillStyle = gradient;
        ctx.rect(0, 0, canvas.width, canvas.height);
        ctx.fill();


        //second canvas--------------------------------------
        let secondCanvas = this.SecondCanvasRef.nativeElement;
        secondCanvas.width = w;
        secondCanvas.height = h;
        let secondCTX: CanvasRenderingContext2D = secondCanvas.getContext('2d');

        //secondCTX.fillStyle = '#C33764';
        //secondCTX.strokeStyle = '#C33764';
        secondCTX.fillStyle = gc[Math.floor(Math.random() * 2)];
        secondCTX.strokeStyle = gc[Math.floor(Math.random() * 2)];
        for (let i = 0; i < 10; i++) {
            let x = Math.random() * (w / 0.9);
            let y = Math.random() * (h / 0.9);
            secondCTX.moveTo(x, y);
            secondCTX.lineWidth = 2;
            secondCTX.shadowOffsetX = 0.5;
            secondCTX.shadowOffsetY = 0.5;
            secondCTX.shadowColor = '#000';
            secondCTX.strokeRect(x, y, 10, 10);
        }

        for (let i = 0; i < 10; i++) {
            let x = Math.random() * (w / 0.9);
            let y = Math.random() * (h / 0.9);
            secondCTX.moveTo(x, y);
            secondCTX.arc(x, y, 5, 0, Math.PI * 2);
        }
        secondCTX.fill();


        for (let i = 0; i < 10; i++) {
            secondCTX.save();
            secondCTX.beginPath();
            let x = Math.random() * (w / 0.9);
            let y = Math.random() * (h / 0.9);
            secondCTX.translate(x, y);
            secondCTX.rotate(Math.random() * 180 * Math.PI / 180);
            secondCTX.moveTo(0, 0);
            secondCTX.lineTo(10, 5);
            secondCTX.lineTo(0, 10);
            secondCTX.closePath();
            secondCTX.stroke();
            secondCTX.restore();
        }
    }

}



