import { Component, Input, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';
import { Title }     from '@angular/platform-browser';

import { CulturalEvent } from './../event-type';

import { GetEventsService } from './../get-events.service';

import { BgPatterns } from './bgpatterns';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';

declare var WebFont;

@Component({
    moduleId: module.id,
    providers: [BgPatterns],
    selector: 'app-event-detail',
    templateUrl: './event-detail.component.html',
    styleUrls: ['./event-detail.component.scss']
})
export class EventDetailComponent implements OnInit {
    events: CulturalEvent[];
    event: CulturalEvent;

    constructor(
        private getEvents: GetEventsService,
        private route: ActivatedRoute,
        private location: Location,
        private bgPatterns: BgPatterns,
        private titleService: Title
    ) { }

    @ViewChild('eventdetaildiv') eventDetailRef: ElementRef;
    @ViewChild('bgcanvas') bgCanvasRef: ElementRef;
    @ViewChild('namecanvas') nameCanvasRef: ElementRef;

    ngOnInit(): void {
        this.route.params
            .switchMap((params: Params) => this.getEvents.getEvent(params['id']))
            .subscribe(event => {
                this.event = event;
                this.titleService.setTitle( event.name + ' | Cultural Events' );
                });

        //------------------------------------------------------------
        let w: number = window.innerWidth;
        let h: number = window.innerHeight;

        let patternNumber: number = Math.floor(Math.random() * 10);
        //let patternNumber: number = 0;

        let bgCanvas = this.bgCanvasRef.nativeElement;
        let bgCTX: CanvasRenderingContext2D = bgCanvas.getContext('2d');

        let bgCanvasW: number = bgCanvas.width;
        let bgCanvasH: number = bgCanvas.height;

        this.bgPatterns.pattern(patternNumber, bgCTX, bgCanvasW, bgCanvasH);

        //-------------------------------------------------------------       
        var that = this;
        let divW = this.eventDetailRef.nativeElement.offsetWidth;

        let nameCanvas = this.nameCanvasRef.nativeElement;
        let nameCanvasW: number = nameCanvas.width;
        let nameCanvasH: number = nameCanvas.height;
        let ctx: CanvasRenderingContext2D = nameCanvas.getContext('2d');


        //rects-----------------------------------------------------------
        that.route.params
            .switchMap((params: Params) => that.getEvents.getEvent(params['id']))
            .subscribe(event => {
                for (var i = 0; i < 9; i++) {
                    ctx.globalAlpha = 0.6;
                    let x: number = Math.random() * (120 - 75) + 75;
                    let rectW: number = Math.random() * (1420 - 1340) + 1340;
                    ctx.fillStyle = event.info.colors[0];
                    if (i > 4) {
                        ctx.fillStyle = event.info.colors[1];
                    }

                    ctx.fillRect(x, 100 + (i * 20), rectW, 40);
                    ctx.globalAlpha = 1;
                }


                //brush strokes---------------------------------------------------        
                /*for (var i = 0; i < 19; i++) {
                    ctx.beginPath();
                    //Math.random() * (max - min) + min;
                    let x: number = Math.random() * (50 - 45) + 45;
                    let y: number = 50 + (i * (Math.random() * (5 - 4) + 4));
                    let cx1: number = x;
                    let cy1: number = y + Math.random() * (12 - 10) + 10;
                    let cx2: number = Math.random() * (720 - 680) + 680;
                    let cy2: number = y + (10 + i);
                    let x2: number = Math.random() * (750 - 710) + 710;
                    let y2: number = y + Math.random() * (5 - 4) + 4;
                    ctx.strokeStyle = 'red';
                    if (i > 9) {
                        ctx.strokeStyle = "blue";
                    }
                    ctx.lineWidth = 5;
                    ctx.moveTo(x, y);
                    ctx.bezierCurveTo(cx1, cy1, cx2, cy2, x2, y2);
                    ctx.stroke();
                }*/

                /*ctx.strokeStyle = 'red';
                ctx.lineWidth = 10;
                ctx.moveTo(50, 50);
                ctx.bezierCurveTo(50, 70, 720, 70, 750, 70);
                ctx.moveTo(50, 60);
                ctx.bezierCurveTo(50, 75, 720, 90, 750, 75);
                ctx.moveTo(50, 70);
                ctx.bezierCurveTo(50, 80, 720, 110, 750, 80);
                ctx.stroke();*/

                //Name------------------------------------------------------------


                let printName: any = function () {
                ctx.beginPath();
                ctx.fillStyle = event.info.colors[2];
                ctx.strokeStyle = event.info.colors[3];
                ctx.lineWidth = 4;
                ctx.font = '200px "Ramabhadra", sans-serif';
                ctx.textAlign = 'center';
                let nameLength: number = event.shortName.length;
                if (nameLength >= 13 && nameLength < 14) {
                    ctx.font = '180px "Ramabhadra", sans-serif';
                } else if (nameLength >= 14 && nameLength < 16) {
                    ctx.font = '170px "Ramabhadra", sans-serif';
                } else if (nameLength >= 16) {
                    ctx.font = '135px "Ramabhadra", sans-serif';
                    ctx.fillText(event.shortName.toUpperCase(), nameCanvasW / 2, nameCanvasH / 1.55);
                    ctx.strokeText(event.shortName.toUpperCase(), nameCanvasW / 2, nameCanvasH / 1.55);
                    return;
                }
                ctx.fillText(event.shortName.toUpperCase(), nameCanvasW / 2, nameCanvasH / 1.5);
                ctx.strokeText(event.shortName.toUpperCase(), nameCanvasW / 2, nameCanvasH / 1.5);
            };

        WebFont.load({
            google: {
                families: ['Ramabhadra']
            },
            active: function() { printName() }
        });

    })
}
}



