export class BgPatterns {
    pattern(patternNum: number, ctx: any, w: number, h: number): void {

        if (patternNum == 0) {
            ctx.lineWidth = 10;
            let multiplyer = 25;
            for (var i = 0; i < 50; i++) {
                ctx.beginPath();
                ctx.strokeStyle = 'rgba(239, 233, 210, 0.3)';
                ctx.moveTo(0, i * multiplyer);
                ctx.lineTo(w, i * multiplyer);
                ctx.stroke();
                ctx.beginPath();
                ctx.moveTo(i * multiplyer, 0);
                ctx.lineTo(i * multiplyer, w);
                ctx.stroke();
            }
        }

        if (patternNum == 1) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            for (var i = 0; i < 7; i++) {
                for (var j = 0; j < 15; j++) {
                    ctx.fillStyle = '#eeeeee';
                    ctx.beginPath();
                    if (i % 2 == 1) {
                        ctx.moveTo(j * 100 + 50, i * 100 + 25);
                        ctx.arc(j * 100 + 50, i * 100 + 25, 30, 0, Math.PI * 2);
                    } else {
                        ctx.moveTo(j * 100, i * 100 + 25);
                        ctx.arc(j * 100, i * 100 + 25, 30, 0, Math.PI * 2);
                    }
                    ctx.fill();
                }
            }
        }

        if (patternNum == 2) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            for (var i = 0; i < 5; i++) {
                for (var j = 0; j < 15; j++) {
                    ctx.fillStyle = '#e5e2c2';
                    ctx.beginPath();
                    if (i % 2 == 1) {
                        ctx.moveTo(j * 100 + 50, i * 100 + 25);
                        ctx.arc(j * 100 + 50, i * 100 + 25, 5, 0, Math.PI * 2);
                    } else {
                        ctx.moveTo(j * 100, i * 100 + 25);
                        ctx.arc(j * 100, i * 100 + 25, 5, 0, Math.PI * 2);
                    }
                    ctx.fill();
                }
            }
        }

        if (patternNum == 3) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            for (var i = 0; i < 15; i++) {
                for (var j = 0; j < 30; j++) {
                    ctx.fillStyle = '#e5e2c2';
                    ctx.beginPath();
                    if (i % 2 == 1) {
                        ctx.moveTo(j * 50 + 23, i * 50);
                        ctx.arc(j * 50 + 23, i * 50, 5, 0, Math.PI * 2);
                    } else {
                        ctx.moveTo(j * 50, i * 50);
                        ctx.arc(j * 50, i * 50, 5, 0, Math.PI * 2);
                    }
                    ctx.fill();
                }
            }
        }

        if (patternNum == 4) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            for (var i = 0; i < 30; i++) {
                ctx.strokeStyle = 'black';
                ctx.lineWidth = 1;
                ctx.beginPath();
                ctx.moveTo(i * 50, h);
                ctx.lineTo(i * 50 - 150, 0);
                ctx.stroke();
            }
        }

        if (patternNum == 5) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            for (var i = 0; i < 30; i++) {
                ctx.strokeStyle = 'black';
                ctx.lineWidth = 1;
                ctx.beginPath();
                ctx.moveTo(i * 50 - 100, h);
                ctx.lineTo(i * 50 + 50, 0);
                ctx.stroke();
            }
        }

        /*if (patternNum == 6) {
            ctx.fillStyle = 'teal';
            ctx.fillRect(0, 0, w, h);
            ctx.strokeStyle = 'black';
            ctx.lineWidth = 5;
            for (var i = 0; i < 30; i++) {
                ctx.beginPath();
                ctx.moveTo(i * 50 - 100, h);
                ctx.lineTo(i * 50 + 100, 0);
                ctx.stroke();
            }
            for (var i = 0; i < 30; i++) {
                ctx.beginPath();
                ctx.moveTo(i * 50, h);
                ctx.lineTo(i * 50 - 200, 0);
                ctx.stroke();
            }
        }*/

        if (patternNum == 6) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            ctx.strokeStyle = 'black';
            ctx.lineWidth = 1;
            for (var i = 0; i < 30; i++) {
                ctx.beginPath();
                ctx.moveTo(i * 50 - 400, h);
                ctx.lineTo(i * 50 + 400, 0);
                ctx.stroke();
            }
            for (var i = 0; i < 30; i++) {
                ctx.beginPath();
                ctx.moveTo(i * 50, h);
                ctx.lineTo(i * 50 - 400, 0);
                ctx.stroke();
            }
        }

        if (patternNum == 7) {
            ctx.strokeStyle = 'black';
            ctx.lineWidth = 1;
            for (var i = 0; i < 21; i++) {
                ctx.beginPath();
                ctx.moveTo(0, h * (i/21));
                ctx.lineTo(w, h * (i / 21));
                ctx.stroke();
            }
        }

        if (patternNum == 8) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            for (var i = 0; i < 15; i++) {
                for (var j = 0; j < 30; j++) {
                    ctx.fillStyle = '#e5e2c2';
                    ctx.beginPath();
                    if (i % 2 == 1) {
                        ctx.moveTo(j * 50 + 23, i * 50);
                        ctx.arc(j * 50 + 23, i * 50, 2, 0, Math.PI * 2);
                    } else {
                        ctx.moveTo(j * 50, i * 50);
                        ctx.arc(j * 50, i * 50, 4, 0, Math.PI * 2);
                    }
                    ctx.fill();
                }
            }
        }

        if (patternNum == 9) {
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, w, h);
            for (var i = 0; i < 60; i++) {
                for (var j = 0; j < 30; j++) {
                    ctx.fillStyle = '#cccccc';
                    ctx.beginPath();
                    if (i % 2 == 1) {
                        ctx.moveTo(j * 50 + 23, i * 50);
                        ctx.arc(j * 50 + 23, i * 10, 2, 0, Math.PI * 2);
                    } else {
                        ctx.moveTo(j * 50, i * 50);
                        ctx.arc(j * 50, i * 10, 2, 0, Math.PI * 2);
                    }
                    ctx.fill();
                }
            }
        }

    }
}


